import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';

import { CitiesService } from "./cities.service";

@Component({
  selector: 'addcity-form',
  templateUrl: './addcity-form.component.html'
})
export class AddCityFormComponent implements OnInit {  
  cities: any[];
  name: string;
  description: string;
  
  constructor(private _cityService: CitiesService, private router: Router) { }

  ngOnInit() {
    this.getCities();   
  }

  getCities() {
    this._cityService.getCities().subscribe(
      (cities:any) =>  {console.log("setting fetched cities");this.cities = cities;},
      err => console.log(err)
    );
  }
  onSubmit(formValue: any){
    console.log("Form Value = " + JSON.stringify(formValue, null, 4));    
    let newCity = {     
          name: formValue.name,     
          description: formValue.description
        };
    this._cityService.addCity(newCity).subscribe(res => {
      console.log("City Added successfully");
      this.getCities();
      this.router.navigate(['admin/cities']);
    }, err => console.log(err));    
  }
}
