import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { UserService } from '../user.service';
import { DatePipe } from '@angular/common';
import * as jwt_decode from "jwt-decode";
import { LoaderService } from 'src/app/loader.service';

@Component({
  selector: 'app-bookhistory',
  templateUrl: './bookhistory.component.html',
  styleUrls: ['./bookhistory.component.css']
})

export class BookhistoryComponent implements OnInit {
  constructor(private router: Router, private userService: UserService, private loaderService: LoaderService) {
  }

  searchText: string;

  searchIssues(val: string) {
    this.searchText = val;
  }


  ngOnInit() {
    this.getBookings();
  }

  bookings: any[];

  getBookings(): void {
    this.loaderService.display(true);
    let email: string;

    if (localStorage.getItem('userType') == 'social' || localStorage.getItem('userType') == 'socialFirstTime') {
      email = localStorage.getItem('socialEmail');
    }
    else {
      email = JSON.parse(JSON.stringify(jwt_decode(localStorage.getItem('token')))).email;
    }
    this.userService.getBookings(email)
      .subscribe(data => {
        this.loaderService.display(false);
        this.bookings = data;
      }, error => {
        this.loaderService.display(false);
        if (error.status === 404) {
          alert("No Bokkings Present in the database...");
        };
      })
  }
}