import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, AbstractControl } from "@angular/forms";
import { Router } from "@angular/router";
import { Validators } from '@angular/forms';
import * as jwt_decode from "jwt-decode";
import { LoginService } from '../login.service';
import { AdminauthGuard } from '../admin/admin.guard';
import { UserguardGuard } from '../users/user.guard';
import { LoaderService } from '../loader.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {


  useDetail: [];
  errorArr: [];
  signInFlag: boolean = true;
  signUpFlag: boolean = false;
  adminFlag: boolean = false;
  // constructor(private router: Router , private loginService : LoginService,private adminGuard : AdminauthGuard,private empGuard:EmpguardGuard) {
  // }

  constructor(private router: Router, private loginService: LoginService, private adminGuard: AdminauthGuard, private userGuard: UserguardGuard, private loaderService: LoaderService) {
    this.signUpForm.controls.password.valueChanges
      .subscribe(
        x => this.signUpForm.controls.confirmPassword.updateValueAndValidity()
      );
  }

  // ngOnInit() {
  //   this.adminGuard.loggedIn(false);  
  //   this.empGuard.loggedIn(false);

  // }

  ngOnInit() {
    this.adminGuard.loggedIn(false);
    this.userGuard.loggedIn(false);

  }
  inputForm = new FormGroup({
    'username': new FormControl('', [Validators.required]),
    'password': new FormControl('', [Validators.required])
  })

  adminForm = new FormGroup({
    'username': new FormControl('admin@gmail.com', [Validators.required]),
    'password': new FormControl('', [Validators.required])
  })

  emailPattern = "^[_A-Za-z0-9-\\+]+(\\.[_A-Za-z0-9-]+)*@" + "[A-Za-z0-9-]+(\\.[A-Za-z0-9]+)*(\\.[A-Za-z]{2,})$";
  specPattern = "^[a-zA-Z ]*$";
  phonePattern = "^([0|\+[0-9]{1,5})?([7-9][0-9]{9})$";
  expPattern = "^(2[01]|[12][0-9]|[1-9])$ ";
  passwordPattern = "^.{8,12}$";

  signUpForm = new FormGroup({
    email: new FormControl('', [Validators.required, Validators.pattern(this.emailPattern)]),
    firstName: new FormControl('', [Validators.required, Validators.pattern(this.specPattern)]),
    lastName: new FormControl('', [Validators.required, Validators.pattern(this.specPattern)]),
    password: new FormControl('', [Validators.required, Validators.pattern(this.passwordPattern)]),
    confirmPassword: new FormControl('', [this.passValidator, Validators.required]),
    location: new FormControl('', Validators.required),
    phoneNumber: new FormControl('', [Validators.required, Validators.pattern(this.phonePattern)])
  });

  public authServerBaseUrl = 'http://localhost:3000';
  public config = {
    "loginRoute": "login",
    "linkedin": {
      "authEndpoint": this.authServerBaseUrl + "/auth/linkedin",
      "clientId": "81nv211t2onjlb",
      //  "redirectURI" : this.authServerBaseUrl+"/auth/linkedin/callback"
      "redirectURI": "http://127.0.0.1:3000/auth/linkedin"
      //"redirectURI" : "https://127.0.0.1:4200/login"
    }
  };

  SignIn() {
    this.adminFlag = false;
    this.signInFlag = true;
    this.signUpFlag = false;
    //alert("inside signin");
  }

  SignUp() {
    this.adminFlag = false;
    this.signInFlag = false;
    this.signUpFlag = true;
    //alert("inside signup");
  }

  LoginAsAdmin() {
    this.adminFlag = true;
    this.signInFlag = false;
    this.signUpFlag = false;
    // alert("inside admin");
  }


  //public auth(provider:string, authConfig: any):void{

  social() {
    this.userGuard.loggedIn(true);
    this.loginService.auth(this.config);
  }

  passValidator(control: AbstractControl) {
    if (control && (control.value != null || control.value !== undefined)) {
      const cnfPassword = control.value;
      const passControl = control.root.get('password');

      if (passControl) {
        const passValue = passControl.value;
        if (passValue !== cnfPassword || passValue === '') {
          return {
            isError: true
          };
        };
        return null;
      }
    }
  }


  login(inputForm: FormData) {
    this.loaderService.display(true);
    this.loginService.loginUser(inputForm)
      .subscribe(
        data => {
          this.loaderService.display(false);
          localStorage.clear();
          let decodedTokenRole = JSON.parse(JSON.stringify(jwt_decode(data))).role;
          localStorage.setItem('token', data.toString());
          if (JSON.parse(JSON.stringify(jwt_decode(data))).role === 'user') {
            this.userGuard.loggedIn(true);
            this.router.navigate(['user']);
          }
          else {
            this.adminGuard.loggedIn(true);
            this.router.navigate(['admin']);
          }

        },
        error => {
          this.loaderService.display(false);
          localStorage.clear();
          alert(error.error.message);
        }
      )
  };

  addUser(signUpForm: FormData) {
    this.loaderService.display(true);
    this.loginService.getUsersByName(this.signUpForm.value.email)
      .subscribe((data) => {
        this.loaderService.display(false);
        alert("User with this e mail already exist , please use different email id");
      },
        (error) => {
          this.loaderService.display(false);
          this.loginService.registerApplication(signUpForm)
            .subscribe(data => {
              alert("User registered successfully, please check your mailbox.");
              this.SignIn();
              //this.router.navigate(['/admin/dashEmployee']);
            });
        })
  };

  protected handleServerError(error: any) {
    this.errorArr = [];
    if (error.status === 500 || error.status === 400) {
      alert(error.error.errorMessage);
    }
    else if ((error.status === 404 && this.inputForm.value.username != 'admin') || error.status === 422) {
      alert("There is no such user !!!");
      this.router.navigate['/login'];
    }
    else if (error.status === 406) {
      error.error.error.forEach(element => {
        alert(element);
      });
    }
  }

  protected handleSuccessErrorGrowls(data: any) {
    if (data['message']) {
      alert(data['message']);
    } else if (data['success']) {
      alert(data['message']);
    } else if (data['info']) {
      alert(data['message']);
    } else {
      alert(data['message']);
    }
  }
}