import { Component, OnInit } from '@angular/core';
import { CitiesService } from "./cities.service";

@Component({
  selector: 'app-cities',
  templateUrl: './cities.component.html',
  styleUrls: ['./cities.component.css']
})
export class CitiesComponent implements OnInit {
  cities: any[];  
  filteredCities: any[];
  title: string;  
  nameFilter: string = "";
  constructor(private _cityService: CitiesService) { }

  ngOnInit() {
    this.getCities();
    console.log(this.cities);
  }

  setNameProperties(nameFilter: string) {    
    this.nameFilter = nameFilter;
    this.filterCity();
  }

  filterCity() {    
    this.filteredCities = this.cities.filter(city => city.name.toLowerCase().startsWith(this.nameFilter.toLowerCase()));  }

  getCities() {
    this._cityService.getCities().subscribe(
      (cities:any) => {this.cities = cities;this.setNameProperties("")},
      err => console.log(err)
    );
  }

  deleteCities() {
    this.cities.forEach(city => {
      if (city.selected) {
        this._cityService.deleteCity(city.id).subscribe(
          (data:any) =>  {
            console.log("City deleted successfully.");
            this.getCities();
          },
        err => console.log(err)
      );
      }
    });   
  }

  deleteCity(cityId: any) {
    this._cityService.deleteCity(cityId).subscribe(
      (data:any) =>  {
        console.log("City deleted successfully.");
        this.getCities();
      },
    err => console.log(err)
  );   
  }
}
