import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';

import { CitiesService } from "../cities/cities.service";
import { BusesService } from "./buses.service";

@Component({
  selector: 'addbus-form',
  templateUrl: './addbus-form.component.html'
})
export class AddBusFormComponent implements OnInit {  
  cities: any[];
  buses: any[];
  route: string;
  description: string;
  fromCity: string;
  toCity: string;
  totalSeats: number;
  constructor(private _citiesService: CitiesService, private _busesService: BusesService, private router: Router) { 
   
  }

  ngOnInit() {
    this.getCities();  
    this.getBuses();     
  }

  getCities() {
    this._citiesService.getCities().subscribe(
      (cities:any) =>  {console.log("setting fetched cities: " + JSON.stringify(cities));this.cities = cities;this.fromCity = this.cities[0];this.toCity = this.cities[0];},
      err => console.log(err)
    );
  }

  getBuses() {
    this._busesService.getBuses().subscribe(
      (buses:any) =>  {console.log("setting fetched buses");this.buses = buses;},
      err => console.log(err)
    );
  }
  onSubmit(formValue: any){
    console.log("Form Value = " + JSON.stringify(formValue, null, 4));    
    let newBus = {     
          route: formValue.route,     
          description: formValue.description,
          fromCity: formValue.fromCity.name,
          toCity: formValue.toCity.name,
          totalSeats: formValue.totalSeats
        };
    this._busesService.addBus(newBus).subscribe(res => {
      console.log("Bus Added successfully");
      this.getBuses();
      this.router.navigate(['admin/buses']);
    }, err => console.log(err));    
  }
}
