import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import { CommonModule, DatePipe } from '@angular/common';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { LoginComponent } from './login/login.component';
import { HttpClientModule } from '@angular/common/http';
import { UsersComponent } from './users/users.component';
import { BookbusComponent } from './users/bookbus/bookbus.component';
import { BookhistoryComponent } from './users/bookhistory/bookhistory.component';
import { Routes, RouterModule } from '@angular/router';
import { OwlDateTimeModule, OwlNativeDateTimeModule } from 'ng-pick-datetime';
import { BusPipe } from './users/bus.pipe';
import { AdminComponent } from './admin/admin.component';
import { AboutComponent } from './admin/about/about.component';
import { CitiesComponent } from './admin/cities/cities.component';
import { CityComponent } from './admin/cities/city.component';
import { AddCityFormComponent } from './admin/cities/addcity-form.component';
import { EditCityFormComponent } from './admin/cities/editcity-form.component';
import { CitiesService } from './admin/cities/cities.service';
import { BusesComponent } from './admin/buses/buses.component';
import { BusComponent } from './admin/buses/bus.component';
import { AddBusFormComponent } from './admin/buses/addbus-form.component';
import { EditBusFormComponent } from './admin/buses/editbus-form.component';
import { BusesService } from './admin/buses/buses.service';
import {AdminauthGuard} from './admin/admin.guard';
import {UserguardGuard} from './users/user.guard';
import { LoaderService } from './loader.service';

const appRoutes: Routes = [
  { path: 'login', component: LoginComponent },
    { path: 'user', component: UsersComponent,
       
        children : [
         { path :  'bookBus' ,component : BookbusComponent,
         canActivate :[UserguardGuard]},
         { path :  'bookings' ,component :  BookhistoryComponent,
         canActivate :[UserguardGuard]}
        ]
      },
      {path :'admin',component :AdminComponent,
      canActivate :[AdminauthGuard],
    children :[
      { path: 'admin', component: AboutComponent },
      { path: 'cities', component: CitiesComponent },
      { path: 'cities/:id', component: CityComponent },
      { path: 'addCity', component: AddCityFormComponent},
      { path: 'editCity/:id', component: EditCityFormComponent},
      { path: 'buses', component: BusesComponent },
      { path: 'buses/:id', component: BusComponent },
      { path: 'addBus', component: AddBusFormComponent},
      { path: 'editBus/:id', component: EditBusFormComponent}
    ]}
 ];
@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    UsersComponent,
    BookbusComponent,
    BookhistoryComponent,
    BusPipe,
    AdminComponent,
    AboutComponent,
    CitiesComponent,
    CityComponent,
    AddCityFormComponent,
    EditCityFormComponent,
    BusesComponent,
    BusComponent,
    AddBusFormComponent,
    EditBusFormComponent
  ],
  imports: [
    RouterModule.forRoot(
      appRoutes,
      { enableTracing: true } 
    ),
    BrowserModule,
    BrowserAnimationsModule,
    AppRoutingModule,
    ReactiveFormsModule,
    FormsModule,
    HttpClientModule,
    OwlDateTimeModule, 
    OwlNativeDateTimeModule,
  ],
  providers: [DatePipe,CitiesService, BusesService,LoaderService],
  bootstrap: [AppComponent]
})
export class AppModule { }
