import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class UserService {

  private baseUrl =    'http://localhost:3000/bus';
  private bookingUrl = 'http://localhost:3000/booking';

  constructor(private http :HttpClient) { 

  }
   getBuses(): Observable<any> {
    return this.http.get(`${this.baseUrl}/buses`);
  }

  getBusStatus(busid :number,bdate:String): Observable<any> {
    console.log("busid" + busid);
    console.log("bdate is " + bdate);
  return this.http.get(this.bookingUrl + '/busStatus' +  '/' + busid + '/' + bdate);
  }

  getBookings(email:String): Observable<any> {
  return this.http.get(this.bookingUrl + '/userBookings' +  '/' + email);
  }

  doBooking(booking :any): Observable<any> {
  return this.http.post(this.bookingUrl + '/doBooking',booking);
  }
}
