import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';

import { CitiesService } from "./cities.service";

@Component({
  selector: 'editcity-form',
  templateUrl: './editcity-form.component.html'
})
export class EditCityFormComponent implements OnInit{  

  constructor(private _cityService: CitiesService, private route: ActivatedRoute, private router: Router) { }
  id: any;
  city: any = {};
  cities:any[];

  ngOnInit(): void {
      this.route.params.forEach((params: Params) => {
          this.id = params['id'];
          console.log("Obtained id: " + this.id);
      });
      this._cityService.getCity(this.id).subscribe(
        (city:any) =>  this.city = city,
        err => console.log(err)
      );
  }  

  getCities() {
    this._cityService.getCities().subscribe(
      (cities:any) =>  this.cities = cities,
      err => console.log(err)
    );
  }

  onSubmit(formValue: any){
    console.log("Form Value = " + JSON.stringify(formValue, null, 4));
    console.log("Before constructing updatedCity");
    let updatedCity = {
          id: this.city.id,
          name: formValue.name,
          description: formValue.description
        };
        console.log("After constructing updatedCity");
    this._cityService.updateCity(updatedCity).subscribe(res => {
      console.log("Updated successfully");
      this.router.navigate(['admin/cities']);
    }, err => console.log(err));    
  }
}
