import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Injectable()
export class CitiesService {
  private _citiesUrl = "http://localhost:3000/city";
  private httpOptions = {
    headers: new HttpHeaders({
      'Content-Type': 'application/json'
    })
  };


  constructor(private _http: HttpClient) {
  }

  getCities() {
    console.log("Getting cities from service");
    return this._http.get(this._citiesUrl);
  }

  getCity(id: any) {
    let getCityURL = `${this._citiesUrl}/${id}`;
    return this._http.get(getCityURL);
  }

  addCity(newCity: any) {    
    return this._http.post(this._citiesUrl, newCity, this.httpOptions);
  }

  updateCity(updatedCity: any) {
    let editCityURL = `${this._citiesUrl}/${updatedCity.id}`;
    return this._http.put(editCityURL, updatedCity, this.httpOptions);
  }

  deleteCity(id: any) {
    let deleteCityURL = `${this._citiesUrl}/${id}`;
    return this._http.delete(deleteCityURL);
  }
}
