import { Component, OnInit } from '@angular/core';
import { CitiesService } from "../cities/cities.service";
import { BusesService } from "./buses.service";

@Component({
  selector: 'app-buses',
  templateUrl: './buses.component.html',
  styleUrls: ['./buses.component.css']
})
export class BusesComponent implements OnInit {
  buses: any[]; 
  filteredBuses: any[]; 
  cities: any[];  
  title: "Add/Edit Bus: ";  
  routeFilter: string = "";
  fromCityFilter: string = "";
  toCityFilter: string = "";
  constructor(private _busService: BusesService, private _cityService: CitiesService) { }

  ngOnInit() {
    this.getCities();
    // this.getBuses();
    
    console.log(this.buses);
  }

  setRouteProperties(routeFilter: string) {    
    this.routeFilter = routeFilter;
    this.filterBus();
  }

  setFromCityProperties(fromCityFilter: string) {    
    this.fromCityFilter = fromCityFilter;
    this.filterBus();
  }

  setToCityProperties(toCityFilter: string) {    
    this.toCityFilter = toCityFilter;
    this.filterBus();
  }

  filterBus() {    
    this.filteredBuses = this.buses.filter(bus => bus.route.toLowerCase().startsWith(this.routeFilter.toLowerCase()) 
    && bus.fromCityName.toLowerCase().startsWith(this.fromCityFilter.toLowerCase()) && bus.toCityName.toLowerCase().startsWith(this.toCityFilter.toLowerCase()));
  }
  
  getCities() {
    this._cityService.getCities().subscribe(
      (cities:any) => {this.cities = cities; this.getBuses()},
      err => console.log(err)
    );
  }
 
  getBuses() {
    this._busService.getBuses().subscribe(
      (buses:any) => {this.buses = buses;this.populateBusName();this.setRouteProperties("");
        this.setFromCityProperties("");this.setToCityProperties("");},
      err => console.log(err)
    );
  }

  populateBusName() {
    this.buses.map((bus) => 
      {
        bus.fromCityName = this.getCityName(bus.fromCity);       
        bus.toCityName = this.getCityName(bus.toCity);       
        return bus;
      }
    );
  }

  getCityName(id) {    
    let cityName = "";
    this.cities.forEach(city => {
      if (city.id === id) {        
        cityName = city.name;             
      }
    });    
    return cityName;
  }

  deleteBuses() {
    this.buses.forEach(bus => {
      if (bus.selected) {
        this._busService.deleteBus(bus.id).subscribe(
          (data:any) =>  {
            console.log("Bus deleted successfully.");
            this.getBuses();
          },
        err => console.log(err)
      );
      }
    });   
  }

  deleteBus(busId: any) {
    this._busService.deleteBus(busId).subscribe(
      (data:any) =>  {
        console.log("Bus deleted successfully.");
        this.getBuses();
      },
    err => console.log(err)
  );   
  }
}
