import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { UserService } from '../user.service';
import { DateValidation } from './DateValidation';
import { OwlDateTimeModule, OwlNativeDateTimeModule } from 'ng-pick-datetime';
import { DatePipe } from '@angular/common';
import * as jwt_decode from "jwt-decode";
import { LoginService } from 'src/app/login.service';
import { LoaderService } from 'src/app/loader.service';

@Component({
  selector: 'app-bookbus',
  templateUrl: './bookbus.component.html',
  styleUrls: ['./bookbus.component.css']
})
export class BookbusComponent implements OnInit {

  constructor(private router: Router, private userService: UserService, private loginService: LoginService, private datePipe: DatePipe, private loaderService: LoaderService) {
    this.flagM = true;
    this.flagT = false;
    this.showSeats = false;
    this.dateSelect = true;
    this.emailFlag = false;
  }


  ngOnInit() {
    this.min = new Date();
    this.min.setDate(this.min.getDate());
    this.getBuses();
  }

  buses: any[];
  flagT: boolean = false;
  flagM: boolean = false;
  emailFlag: boolean = false;
  showSeats: boolean = false;
  dateSelect: boolean = true;
  seatsLeft: number;
  applyForm: FormData;
  min: Date;
  searchText: string;

  emailPattern = "^[_A-Za-z0-9-\\+]+(\\.[_A-Za-z0-9-]+)*@" + "[A-Za-z0-9-]+(\\.[A-Za-z0-9]+)*(\\.[A-Za-z]{2,})$";
  searchForm = new FormGroup({
    email: new FormControl('', [Validators.required, Validators.pattern(this.emailPattern)]),
    busid: new FormControl('', Validators.required),
    totalSeats: new FormControl('', Validators.required),
    route: new FormControl('', Validators.required),
    seats: new FormControl('', Validators.required),
    bdate: new FormControl('', [Validators.required, DateValidation.ValidDate])
  }
  );

  empForm = new FormData();
  socialForm = new FormData();

  bookBus(bus: FormData): void {
    let myData = JSON.stringify(bus);
    let myData1 = JSON.parse(myData);
    this.empForm.append('busid', myData1['id']);
    this.empForm.append('seats', myData1['totalSeats']);
    this.empForm.append('route', myData1['route']);
    this.flagM = false;
    this.flagT = true;
  }

  cancel(): void {

    this.flagM = true;
    this.flagT = false;
  }

  getBuses(): void {
    this.userService.getBuses()
      .subscribe(data => {
        this.flagM = true;
        this.flagT = false;
        this.buses = data;
      }, error => {
        if (error.status === 404) {
          alert("No Buses Present in the database");
        };
      })
  }

  searchIssues(val: string) {
    this.searchText = val;
  }

  dateEntered() {
    if (this.searchForm.controls.bdate.valid) {
      this.showSeats = true;
      this.dateSelect = false;
      let bdate = this.datePipe.transform(this.searchForm.value.bdate, 'yyyy-MM-dd');
      var object = {};
      this.empForm.forEach(function (value, key) {
        object[key] = value;
      });
      var json = JSON.stringify(object);
      let myData = JSON.stringify(this.empForm);
      let myData1 = JSON.parse(myData);
      this.userService.getBusStatus(JSON.parse(json).busid, bdate)
        .subscribe(data => {
          let a = data.seats;
          this.seatsLeft = (JSON.parse(json).seats - a);

          if (localStorage.getItem('userType') == 'socialFirstTime') {
            this.emailFlag = true;
          }

          this.empForm.append('bdate', bdate);

        }, error => {
          if (error.status === 404) {
            alert("No Buses Present in the database");
          };
        })
    }
  }


  onSubmit() {
    this.loaderService.display(true);
    let email: any;
    if (localStorage.getItem('userType') == 'socialFirstTime') {
      email = this.searchForm.value.email;
      localStorage.setItem('socialEmail',email);
      this.socialForm.append('email', email);
      let socialId = JSON.parse(JSON.stringify(jwt_decode(localStorage.getItem('token')))).id;
      this.socialForm.append('socialId', socialId);

      var object1 = {};
      this.socialForm.forEach(function (value, key) {
        object1[key] = value;
      });
      var json1 = JSON.stringify(object1);
      this.loginService.registerSocialUser(JSON.parse(json1))
        .subscribe(data => {
        },
          error => {
          })
    }

    if (localStorage.getItem('userType') == 'social') {
      email = localStorage.getItem('socialEmail');
    }


    if (localStorage.getItem('userType') == null) {
      email = JSON.parse(JSON.stringify(jwt_decode(localStorage.getItem('token')))).email;
    }

    this.empForm.append('email', email);
    if (this.searchForm.value.seats > 3) {
      this.loaderService.display(false);
      alert("Seats Must be bwtween 1 and 3 ");
    }
    else {
      if (this.seatsLeft < this.searchForm.value.seats) {
        this.loaderService.display(false);
        alert("Seats Must be less than " + this.seatsLeft);
      }
      else {
        this.empForm.append('seats', this.searchForm.value.seats);
        var object = {};
        this.empForm.forEach(function (value, key) {
          object[key] = value;
        });
        var json = JSON.stringify(object);
        this.userService.doBooking(JSON.parse(json))
          .subscribe(data => {
            this.loaderService.display(false);
            alert("Booking Done, lease check your mailbox");
            this.router.navigate(['user']);
          },
            error => {
              this.loaderService.display(false);
            })
      }
    }
  }
}