import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';

import { CitiesService } from "../cities/cities.service";
import { BusesService } from "./buses.service";

@Component({
  selector: 'editbus-form',
  templateUrl: './editbus-form.component.html'
})
export class EditBusFormComponent implements OnInit{  

  constructor(private _cityService: CitiesService, private _busService: BusesService, private route: ActivatedRoute, private router: Router) { }
  id: any;
  bus: any = {};
  cities:any[];
  buses:any[];
  

  ngOnInit(): void {
      this.route.params.forEach((params: Params) => {
          this.id = params['id'];
          console.log("Obtained id: " + this.id);
      });

      this._busService.getBus(this.id).subscribe(
        (bus:any) =>  {this.bus = bus
        console.log("this is bus" +JSON.stringify(bus));},
        err => console.log(err)
      );

      this.getCities();
  }  

  getCities() {
    this._cityService.getCities().subscribe(
      (cities:any) => {this.cities = cities;},
      err => console.log(err)
    );
  }
 
  onSubmit(formValue: any){
    console.log("Form Value = " + JSON.stringify(formValue, null, 4));
    console.log("Before constructing updatedBus");
    let updatedBus = {
          id: this.bus.id,
          route: formValue.route,
          description: formValue.description,
          fromCity: formValue.fromCity,
          toCity: formValue.toCity,
          totalSeats: formValue.totalSeats
        };
        console.log("After constructing updatedBus");
    this._busService.updateBus(updatedBus).subscribe(res => {
      console.log("Updated successfully");
      this.router.navigate(['admin/buses']);
    }, err => console.log(err));    
  }
}
