import { Component, OnInit } from '@angular/core';
import {Location, LocationStrategy, PathLocationStrategy} from '@angular/common';
import { ActivatedRoute } from "@angular/router";
import * as jwt_decode from "jwt-decode";
import { LoginService } from '../login.service';
import { Router } from "@angular/router";
import { UserguardGuard } from './user.guard';

@Component({
  selector: 'app-users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.css']
})

export class UsersComponent implements OnInit {

  constructor(private router: Router,private route: ActivatedRoute,private location : Location,private loginService:LoginService,private userGuard:UserguardGuard) { }

  ngOnInit() {
  if(this.route.snapshot.queryParamMap.get("token") != null){
    this.userGuard.loggedIn(true);
  const token = this.route.snapshot.queryParamMap.get("token");
  let socialId = JSON.parse(JSON.stringify(jwt_decode(token))).id;
  console.log('socialId is ' + socialId);
  this.loginService.getUsersBySocialId(socialId)
  .subscribe(
    (data) =>{
      localStorage.setItem('userType','social');
      localStorage.setItem('socialEmail',data[0]['email']);
      localStorage.setItem('token',this.route.snapshot.queryParamMap.get("token").toString());
      console.log("usertype at initis " + localStorage.getItem('userType'));
    },
    (error)=>{
      localStorage.setItem('userType','socialFirstTime');
      console.log("usertype at initis " + localStorage.getItem('userType'));
      localStorage.setItem('token',this.route.snapshot.queryParamMap.get("token").toString());
    }
  )
  }
}

logout(){
  localStorage.clear();
  this.router.navigate(['login']);
}
}